import ContentList from './ContentList'
import { connect } from 'react-redux'

const mapStateToProps = (state) => {
  // console.log(state.note)
  return {
    noteItems: state.noteReader.notes
  }
}

const mapDispatchToProps = (dispatch, action) => {
  return {
    setActiveNote: (id, gIndex) => {
      dispatch({
        type: 'R_SET_ACTIVE_NOTE',
        id, gIndex
      })
    },
    setActiveLink: (id) => {
      dispatch({
        type: 'R_SET_ACTIVE_LINK',
        id
      })
    }
  }
}

const ConnectedContentList = connect(
  mapStateToProps,
  mapDispatchToProps
)(ContentList)

export default ConnectedContentList
















