import Note from './index'
import { connect } from 'react-redux'

const mapStateToProps = (state) => {
  return {
    notes: state.notebook.noteBookMeta.notes || []
  }
}

const mapDispatchToProps = (dispatch, action) => {
  return {
    createNoteBook: (id) => {
      if (id === undefined) {
        return console.error('Can not create notebook from undefined id')
      }

      // DEV ------------------------------------------
      if (!id) { // id === null (deliberate pass)
        var payload = {
          noteBookMeta:{
            notes:[
              {
                name: 'dev note 1',
                id: 'dev#1'
              },
              {
                name: 'dev note 2',
                id: 'dev#2'
              },
              {
                name: 'dev note 3',
                id: 'dev#3'
              }
            ]
          }
        }
        dispatch({
          type: 'NOTEBOOK_SET_STATE',
          payload
        })
        return
      }
      // DEV END ------------------------------------------

      // use asynRead to DO the reading
      dispatch({
        type: 'CREATE_NOTEBOOK',
        id
      })
    },

    setNextActive: () => {
      dispatch({
        type: 'R_SET_NEXT_ACTIVE'
      })
    },
    toggleSidebar: () => {
      dispatch({
        type: 'R_TOGGLE_SIDEBAR'
      })
    },
    setNoteExists: () => {
      var payload = {
        noNoteExists: false
      }
      dispatch({
        type: 'NOTEBOOK_SET_STATE',
        payload
      })
    }
  }
}

const VisibleNote = connect(
  mapStateToProps,
  mapDispatchToProps
)(Note)

export default VisibleNote


