import Chance from 'chance';
import localStore from 'store'

var chance = new Chance();
var poolStr = 'abcdefghijklmnopqrstuvwxyz0123456789'

let remoteToLocalMap = localStore.get('remote-local-id-map') || {}

function createNote (dispatch, chapters, data) {
  var index = data.index
  var notes = []
	chapters.forEach((_el)=>{
    var index = -1
    var name = -1
    name = _el.name
    index = name.lastIndexOf('.')
    if (index !== -1) {
      name = name.slice(0, index)
    }
    var localId = remoteToLocalMap[_el.id] || chance.string({length: 32, pool: poolStr})
    notes.push({
      name: name,
      localId: localId,
      remoteId: _el.id,
      introLocalId: remoteToLocalMap[_el.introId] || chance.string({length: 32, pool: poolStr}),
      introRemoteId: _el.introId,
      children: _el.topics.length,
      type: 'chapter',
      edit: false,
      selected: false
    })
    for (var j = 0, i = _el.topics.length; j < i; j++) {
      var el = _el.topics[j]
      name = el.name
      index = name.lastIndexOf('.')
      if (index !== -1) {
        name = name.slice(0, index)
      }
      notes.push({
        name: name,
        localId: remoteToLocalMap[el.id] || chance.string({length: 32, pool: poolStr}),
        remoteId: el.id,
        type: 'topic',
        edit: false,
        selected: false,
        parentId: _el.id,
        parentLocalId: localId
      })
    }
  })
  notes.forEach((_el, _index) => { _el.gIndex = _index })
  if (notes.length) {
    if (index === undefined || index < 0 || !(index < notes.length)) {
      // redirect to /0
      data.history.push(`/note/edit/${data.noteId}/0`)
      index = 0
    }
  }

  // if index < notes.length
  dispatch({
    type: 'NOTEBOOK_SET_STATE',
    payload: {
      noteMeta:{
        noteId: data.noteId,
        chapters
      }
    }
  })

  dispatch({
    type: 'NOTE_SET_STATE',
    payload: {
      notes
    }
  })
  dispatch({
    type: 'SET_ACTIVE_LINK',
    index
  })
  dispatch({
    type: 'SET_ACTIVE_EDITOR',
    index,
  })
  // if (notes[index]) {
  //   if (notes[index].type === 'chapter') {
  //     console.log(notes)
  //     dispatch({
  //       type: 'INIT_EDITOR_FROM_ID',
  //       localId: notes[index].introLocalId,
  //       remoteId: notes[index].introRemoteId || '--------',
  //       noteType: notes[index].type,
  //     })
  //   } else {
  //     dispatch({
  //       type: 'INIT_EDITOR_FROM_ID',
  //       localId: notes[index].localId,
  //       remoteId: notes[index].remoteId
  //     })
  //   }
  // }
}

export default createNote


