import Note from './su-note' 

/* eslint-disable */
import { RemoteNote } from './../../sync_gapi/app/gapi' 
/* eslint-enable */

import createNote from './create-note' 
import { connect } from 'react-redux'

const mapStateToProps = (state) => {
  return {
    activeLink: state.note.activeLink,
    activeLinkIndex: state.note.activeLinkIndex,
    activeEditorLocalId: state.note.activeEditorLocalId,
    activeEditorRemoteId: state.note.activeEditorRemoteId,
    noteItems: state.note.notes,
    showEditor: state.note.showEditor,
    noedit: (state.mdEditor.currentEditorId && !state.mdEditor.editors[state.mdEditor.currentEditorId].edit) || false,
    sidebarVisible: state.note.sidebarVisible,
    editNoteItems: state.notebook.noteMeta.chapters,
    noteId: state.notebook.noteMeta.noteId,
    noNoteExists: state.notebook.noNoteExists,
    authorized: state.notebook.authorized,
    loadingNote: state.note.loadingNote
 }
}

const mapDispatchToProps = (dispatch, action) => {
  return {
    removeTo: () => {
      dispatch({
        type: 'REMOVE_TO_PATH',
      })
    },
    setNotebookState: (payload) => {
      dispatch({
        type: 'NOTEBOOK_SET_STATE',
        payload
      })
    },
    setLoading: () => {
      dispatch({
        type: 'NOTE_SET_STATE',
        payload: {
          loadingNote: true
        }
      })
    },
    // DEV APPROVED
    createNote: (data) => {
      // CREATE THE CHPATERS INSIDE GAPI
      dispatch({
        type: 'NOTE_SET_STATE',
        payload: {
          noteId: data.noteId
        }
      })

      var note = new RemoteNote(data.noteId)
      note.init((err, metadata)=>{
        if (err) {
          console.log(err)
          data.history.push('/notebooks')
          return
        }
        createNote(dispatch, metadata.chapters, data)
      })
      // DEV END -------------------------------------

      
    },
  }
}

const VisibleNote = connect(
  mapStateToProps,
  mapDispatchToProps
)(Note)

export default VisibleNote
















