import React, { Component } from 'react';
import { Route, HashRouter as Router, Switch } from 'react-router-dom';
import ContentList from './../ContentList/conn'
import ContextMenu from './../../ContextMenu/conn'
import './index.scss'

class SidebarContentEditor extends Component {
  addChapter () {
    this.props.addChapter(this.props.noteId)
  }

  componentDidUpdate(prevProps, prevState) {
    var params = this.props.match.params
    var props = this.props
    if (!(params.index < props.noteItems.length) || params.index < 0) {
      if (this.props.match.url !== prevProps.match.url) {
        // console.log('Setting 0 index')
        props.history.push(`/note/edit/${props.noteId}/0`)
      }
    }
  }

  // <div className="note-search">Search</div>
  render () {
    return (
      <div className="sidebar-content-editor">
        <div className="sidebar-toolbar">
          Search, 
        </div>
        {this.props.showContextMenu ? <ContextMenu style={this.props.contextMenuStyle} history={this.props.history} items={this.props.contextItems}/> : ''}
          <Router>
            <Switch>
              <Route path={`/note/edit/:noteId/:index`} component={ContentList}/>
            </Switch>
          </Router>
        {this.props.sidebarVisible ? <span className="add-chapter-btn" onClick={this.addChapter.bind(this)} >Add a chapter</span> : ''}
      </div>
    )
  }
}
export default SidebarContentEditor;


