import store from './../../store'
import syncSave from './../../../sync_gapi/app/gapi';

const onClick = (e) => {
  console.log('clicked')
}

const chapterContextMenu = [
  {
    type: 'ctx-handle',
    name: 'delete chapter',
    onClick: (history) =>{
      var state = store.getState()
      var removeEl = Object.assign({}, state.note.ctxTargetEl)
      store.dispatch({type: 'REMOVE_CHAPTER'})

      var index = -1
      if (removeEl.gIndex > 0) {
        index = removeEl.gIndex - 1
      } else if (removeEl.gIndex < state.note.notes.length - 1) {
        index = removeEl.gIndex + 1        
      }
      if (index !== -1) {
        history.push(`/note/edit/${state.note.noteId}/${index}`)
      }

      // remove from the server
      console.log('Removing from the server')
      console.log(removeEl)
      syncSave.addTask({
        type: 'DELETE_CHAPTER',
        payload: {
          noteId: state.note.noteId,
          name: removeEl.name,
          chapterLocalId: removeEl.localId,
          chapterRemoteId: removeEl.remoteId,
        }
      })
    }
  },
  {
    type: 'ctx-handle',
    name: ['make disabled', 'make enabled'],
    nameIndex: 0,
    onClick: () =>{
      store.dispatch({type: 'TOGGLE_DISABLED'})
    }
  },
  {
    type: 'separator'
  },
  {
    type: 'ctx-handle',
    name: ['add icon', 'remove icon'],
    nameIndex: 0,
    onClick: onClick
  }
]

const topicContextMenu = [
  {
    type: 'ctx-handle',
    name: 'delete topic',
    onClick: (history) =>{
      var state = store.getState()
      var removeEl = Object.assign({}, state.note.ctxTargetEl)

      store.dispatch({type: 'REMOVE_TOPIC'})
      var index = -1
      if (removeEl.gIndex > 0) {
        index = removeEl.gIndex - 1
      } else if (removeEl.gIndex < state.note.notes.length - 1) {
        index = removeEl.gIndex + 1        
      }
      if (index !== -1) {
        history.push(`/note/edit/${state.note.noteId}/${index}`)
      }

      // remove from the server
      console.log('Removing from the server')
      syncSave.addTask({
        type: 'DELETE_TOPIC',
        payload: {
          noteId: state.note.noteId,
          name: removeEl.name,
          topicLocalId: removeEl.localId,
          topicRemoteId: removeEl.remoteId,
          chapterRemoteId: removeEl.parentId,
          chapterLocalId: removeEl.parentLocalId,
        }
      })
    }
  },
  {
    type: 'ctx-handle',
    name: ['make disabled', 'make enabled'],
    nameIndex: 0,
    onClick: (el) =>{
      store.dispatch({type: 'TOGGLE_DISABLED'})
    }
  },
  {
    type: 'separator'
  },
  {
    type: 'ctx-handle',
    name: ['add icon', 'remove icon'],
    nameIndex: 0,
    onClick: onClick
  }
]

export {
  chapterContextMenu,
  topicContextMenu
}


