const editorStore = (state, action) => {
  var obj = {
    editors: {},
    currentEditorLocalId: '',
    currentEditorRemoteId: '',
    loadingData: false
  }

  var editors = {}
  var currentEditor = {}
  var ret  = {}
  var id = ''

  switch(action.type){
    case 'UPDATE_REMOTE_ID':
      editors = state.editors
      for (var editorLocalId in editors) {
        if (editors[editorLocalId].localId === action.localId) {
          editors[editorLocalId].remoteId = action.remoteId
        }
      }
      // console.error('Updating remoteId', action.localId, action.remoteId)
      return Object.assign({}, state, {
        editors 
      })

    case 'INIT_EDITOR_FROM_ID':
      console.log(action)
      if (state.editors[action.localId]) {
        return Object.assign({}, state, {
          currentEditorLocalId: action.localId,
          currentEditorRemoteId: action.remoteId,
        })
      }
      ret = {
          localId: action.localId,
          remoteId: action.remoteId, // for server sync
          preview: true,        // edit mode or preview mod
          dirty: false,
          edit: false,             // determines if editor needs to be rendered
          editing: false,
          edited: false,
          htmlText: '<h2>New Note.</h2>',
          markdownText: '## New Note.'
        }

      editors = Object.assign({}, state.editors, { [action.localId]: ret })      
      // if remoteId exists fetch the data and set
      if (action.remoteId) {
        return Object.assign({}, state, {
          editors,
          currentEditorLocalId: action.localId,
          currentEditorRemoteId: action.remoteId,
        })        
      }
      return Object.assign({}, state, {
        editors,
        currentEditorLocalId: action.localId,
        currentEditorRemoteId: '!!no-remote-id-provided!!',
      })

    case 'SET_EDITOR_DATA_LOADING':
      return Object.assign({}, state, { editorDataLoading: action.flag })

    case 'SET_EDITOR_STATE':
      id = state.currentEditorLocalId 
      if (action.localId)  {
        id = action.localId
      }
      
      delete(action.localId)
      
      currentEditor = state.editors[id]
      currentEditor = Object.assign({}, currentEditor, action.payload)

      console.log('------>>>', action.dataLen === 0)
      
      // If no data is there show in editing mode.
      if (action.dataLen === 0) {
        currentEditor = Object.assign({}, currentEditor, {
          dirty: true,
          edit: true,             // determines if editor needs to be rendered
          editing: true,
          edited: true,
        })
      }
      editors = Object.assign({}, state.editors, {
        [state.currentEditorLocalId]: currentEditor
      })

      return Object.assign({}, state, {
        editors,
        loadingData: action.loadingData
      })

    default:
      return state || obj
  }
}
export default editorStore;


