import React, { Component } from 'react';
import { Link} from 'react-router-dom';

import './index.scss'

import handleGauthLoad from './../../gapi/gapi'
// import NoteBook_ from './../../gapi/notebook'


class NoteBook extends Component {
  constructor (props) {
    super(props)
    this.state = {
      gdrive: false,
      notes: []
    }
    this.noteBook = {}    
  }

  componentWillMount() {
    var self = this
    if (window.env !== 'dev') {
      console.log('Trying to authorize at EL:su-note-book')
      handleGauthLoad((err, res)=>{
        console.log('Authorized...')
        self.props.createNoteBook(res.id)
      })
    } else { // DEV
        self.props.createNoteBook(null)
    }
    // create the notebook
    // show the data
  }

  render() {
    var notes = <h1>Loading</h1>
    if (this.props.notes.length !== 0) {
      notes = this.props.notes.map((note, index)=>{
        var lindex = note.name.lastIndexOf('.')
        var name = note.name.slice(0, lindex)
        console.log(name, note.name)
        return (
          <li className="notebook-li row" key={index}>
            <div className="col md-10">
              <div className="note-name">
                <Link to={`/note/read/${note.id}`}>
                  {name}
                </Link>
              </div>
              <div className="note-info" >
                <span className="author">by Subhasis Maity</span>
                <span className="tags">
                  <span className="tag">money</span>
                  <span className="tag">physics</span>
                  <span className="tag">science</span>
                  <span className="tag">life</span>
                </span>
              </div>
            </div>
            <div className="col md-1">
              <Link to={`/note/edit/${note.id}`}>
                <div className="k-sm-btn">Edit</div>
              </Link>
            </div>
            <div className="col md-1">
              ooo
            </div>
          </li>
        )
      })
    }
    // <div className="k-sm-btn" onClick={window.signIn}>ooo</div>
    // <div className="notebook-toolbar">
    //   <div className="layout">
    //     <div className="notebook-storage">
    //       <ul className="options">
    //         <li className="option-item">
    //           <div className="k-sm-btn" onClick={()=>{this.createNote()}}>create note</div>
    //         </li>
    //         <li className="option-item">
    //           {this.state.gdrive ? <div className="k-sm-btn" onClick={()=>{window.signIn();this.setState({gdrive: true});}}>Gdrive out</div> :
    //           <div className="k-sm-btn" onClick={()=>{window.signOut();this.setState({gdrive: false});}}>Gdrive in</div>}
    //         </li>
    //       </ul>
    //     </div>
    //   </div>
    // </div>

    return (
      <div className="note-book pt-16">
      

        <div className="layout">
          <div className="row">
            <div className="col md-8">
              <ul className="notebook-ul">  
                {notes}
              </ul>
            </div>
            <div className="col md-4">
              ---
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default NoteBook


