import Note from './SidebarContentEditor'
import { connect } from 'react-redux'

const mapStateToProps = (state) => {
  return {
    noteItems: state.note.notes,
    showContextMenu: state.note.showContextMenu,
    contextMenuStyle: state.note.contextMenuStyle,
    contextItems: state.note.contextItems,
    sidebarVisible: state.note.sidebarVisible,
    noteId: state.notebook.noteMeta.noteId,
  }
}

const mapDispatchToProps = (dispatch, action) => {
  return {
    addChapter: (noteId) => {
      dispatch({
        type: 'ADD_NEW_CHAPTER',
        noteId: noteId
      })
    }
  }
}

const ConnectedSideBarConentEditor = connect(
  mapStateToProps,
  mapDispatchToProps
)(Note)

export default ConnectedSideBarConentEditor


