import { Provider } from 'react-redux'
import React, { Component } from 'react';
import { Route, HashRouter as Router, Switch } from 'react-router-dom';

import store, { history } from './../../redux/store'


// import styles
import './index.scss';
import 'bootstrap/dist/css/bootstrap.min.css' // load bootstrap
import 'karicha/styles/css/layout/grid.col.css'
import 'karicha/styles/css/layout/layout.css'
import 'karicha/styles/css/layout/grid.col.offset.css'
import 'karicha/styles/css/helper/border.css'
import 'karicha/styles/css/helper/pad.mar.css'
import 'karicha/styles/css/helper/colors.css'
import 'karicha/styles/css/helper/dimentions.css'
import 'karicha/styles/css/util/fonts.css'
import 'mdi/css/materialdesignicons.min.css';

import Navigation from '../navigation/index'
import LoginPage from '../login-page/index'
import MainPage from '../main-page/index'
import NoteBook from '../su-note-book/conn'
import ReadNote from '../su-note-reader/conn'
import EditNote from '../su-note-editor/conn'

class App extends Component {
  constructor (props) {
    super(props)
    this.data = {
      baseURL: ''
    }
  }

  componentDidMount() {
    // this.baseURL = 'https://holasm.github.io/react-test/noteapp/'
    // this.baseURL = 'https://localhost:3000'
  }

  render() {
    return (
      <Provider store={store}>
        <Router history={history}>
          <div className="no-scroll padding-top-50">
            <Navigation/>
            <Switch>
              <Route path={`/notebooks`} component={NoteBook}/>
              <Route path={`/login`} component={LoginPage}/>
              <Route path={`/note/read/:noteId/:index`} component={ReadNote}/>
              <Route path={`/note/read/:noteId/`} component={ReadNote}/>
              <Route path={`/note/edit/:noteId/:index`} component={EditNote}/>
              <Route path={`/note/edit/:noteId`} component={EditNote}/>
              <Route path={`/`} component={MainPage}/>
            </Switch>
          </div>
        </Router>
      </Provider>
    );
  }
}

export default App;



