import { assertUpdate, log, logStore} from './../utils'
import { updateMetadata, updateTopicRemoteId, updateChapterRemoteId } from './update-future-tasks'
import { caseCreateNote, caseUpdateMetadata } from './note-cases'

const execute =  function (self) {
  logStore.push([self.tasks.length])
  var task = self.tasks[0]
  // update localStorage
  if (task.message) {
    log(task.message)
  }

  console.error('TASK => '+task.type, task.payload)

  switch (task.type) {
    case '@CREATE_NOTE':
      caseCreateNote(self, task)
        // setTimeout(()=>{
        //   log('Create a note from id ', task.payload.noteId)
        //   self.note.noteId = task.payload.noteId
        //   self.next()
        // }, 200)
      break

    case '@CREATE_CHAPTER_FILE': // => @CREATE_CHAPTER_INTRO_FILE
      console.log(self.tasks)
      self.assertNote(task, ()=>{
        // console.log(self.note.metadata)
        assertUpdate(self.note.metadata, task, ()=>{
          log(' -> New chapter created')
          self.note.createChapterSync(task.payload.name, (err, res)=>{
            
            logStore.flushInLine() // flush messages
            console.log(res)
            updateChapterRemoteId(self, task.payload.chapterLocalId, res.id)
            self.next()
          })
          // updateChapterRemoteId(self, task.payload.chapterLocalId, id)
          // self.next()
        }, ()=>{
          self.next(1)
        })

        // setTimeout(()=>{
        //   // update all remoteChapteId
        //   var id = Math.random(); id *= 100; id = Math.floor(id);
        //   updateChapterRemoteId(self, task.payload.chapterLocalId, id)
        //   self.next()
        // }, 200)
      })
      break 

    case '@CREATE_CHAPTER_INTRO_FILE': // => @UPDATE_METADATA
      self.assertNote(task, ()=>{
        assertUpdate(self.note.metadata, task, ()=>{
          self.note.createChapterIntroSync(task.payload.chapterRemoteId, task.payload.name, (err, metadata)=>{
            log(' -> Chapter intro file created.')
            // log(metadata)
            // get the introId and update others
            updateMetadata(self, self.note.noteId, self.note.metadata)
            self.next()
          })
        }, ()=>{
          self.next(1)
        })
      })
      break

    case '@UPDATE_CHAPTER_INTRO_FILE':
      self.assertNote(task, ()=>{
        assertUpdate(self.note.metadata, task, ()=>{
          self.note.updateTopic(task.payload.introId, 'Updated inro', (err, res)=>{
            log(' -> Chapter intro data updated')
            // update file
            // assign metadata update
            updateMetadata(self, task.payload.noteId, self.note.metadata)
            // UPDATE_REMOTE_INTRO_ID
            self.next()
          })
        }, ()=>{
          self.next(1)
        })

        // setTimeout(()=>{
        //   self.next()
        // }, 200)
      })
      break

    case '@DISABLE_CHAPTER':
      self.assertNote(task, ()=>{
        log(' -> Chapter disabled')
        // updateMetadata(self, task.payload.noteId, self.note.metadata)
        setTimeout(()=>{
          self.next()
        }, 200)
      })
      break

    case '@DELETE_CHAPTER_CREATE_METADATA':
      self.assertNote(task, ()=>{
        assertUpdate(self.note.metadata, task, ()=>{
          console.log(task.payload)
          self.note.deleteChapterSync(task.payload.chapterRemoteId, (err, metadata)=>{
            if (err) { log(err); return }
            log(' -> Chapter delete metadata created')
            console.log(metadata)
            updateMetadata(self, self.note.noteId, metadata)
            self.next()
          })
        }, ()=>{
          self.next(1)
        })
      })
      break

    case '@CREATE_TOPIC_FILE':  // => @UPDATE_METADATA

      self.assertNote(task, ()=>{
        assertUpdate(self.note.metadata, task, ()=>{
          log(' -> New topic created')
          self.note.createTopicSync(task.payload.chapterRemoteId, task.payload.name, (err, metadata, id)=>{
            updateTopicRemoteId(self, task.payload.topicLocalId, id)
            updateMetadata(self, task.payload.noteId, self.note.metadata) // for DEV metadata === null
            self.next()
          })
        }, ()=>{
          self.next(1)
        })
      })
      break

    case '@UPDATE_TOPIC_FILE':
      self.assertNote(task, ()=>{
        assertUpdate(self.note.metadata, task, ()=>{
          log(' -> Topic data updated')
          self.note.updateTopic(task.payload.topicRemoteId, task.payload.markdownText, (err, res)=>{
            updateMetadata(self, task.payload.noteId, self.note.metadata)
            self.next()
          })
        }, ()=>{ // skip task
          self.next(1)
        })
      })
      break // => @UPDATE_METADATA

    case '@DISABLE_TOPIC':
      self.assertNote(task, ()=>{
        log(' -> Topic disabled')
        // updateMetadata(self, self.note.noteId, metadata)
        setTimeout(()=>{
          self.next()
        }, 1000)
      })
      break

    case '@DELETE_TOPIC_CREATE_METADATA':
      self.assertNote(task, ()=>{
        assertUpdate(self.note.metadata, task, ()=>{
          self.note.deleteTopicSync(task.payload.topicRemoteId, (err, metadata)=>{
            updateMetadata(self, self.note.noteId, metadata)
            log(' -> Topic delete metadata created')
            self.next()
          })
        }, ()=>{
          self.next(1)
        })
      })
      break

    case '@DELETE_CHAPTER_FILE':
      self.assertNote(task, ()=>{
        assertUpdate(self.note.metadata, task, ()=>{
          log(' -> Chapter file deleted.')
          self.note.deleteFile(task.payload.chapterRemoteId, (err, res)=>{
            self.next()
          })
        }, ()=>{
          self.next(1)
        })
      })
      // caseUpdateMetadata(self, task)
      break // using metaId

    case '@DELETE_TOPIC_FILE':
      self.assertNote(task, ()=>{
        assertUpdate(self.note.metadata, task, ()=>{
          log(' -> Topic file deleted.')
          self.note.deleteFile(task.payload.topicRemoteId, (err, res)=>{
            self.next()
          })
        }, ()=>{
          self.next(1)
        })
      })
      break // using metaId

    case '@RENAME_CHAPTER_CREATE_METADATA':
      self.assertNote(task, ()=>{
        assertUpdate(self.note.metadata, task, ()=>{
          log(' -> Chapter file rename metadata created')
          self.note.renameTopic(task.payload.chapterRemoteId, task.payload.name, (err, metadata)=>{
            updateMetadata(self, self.note.noteId, metadata)
            self.next()
          })
        }, ()=>{
          self.next(1)
        })
      })
      break

    case '@RENAME_CHAPTER_FILE':
      self.assertNote(task, ()=>{
        assertUpdate(self.note.metadata, task, ()=>{
          log(' -> Chapter file renamed.')
          self.note.renameFile(task.payload.chapterRemoteId, task.payload.name + '.chapter', (err, metadata)=>{
            self.next()
          })
        }, ()=>{
          self.next(1)
        })
      })
      break

    case '@RENAME_TOPIC_CREATE_METADATA':
      self.assertNote(task, ()=>{
        assertUpdate(self.note.metadata, task, ()=>{
          log(' -> Topic file rename metadata created.')
          self.note.renameTopic(task.payload.topicRemoteId, task.payload.name, (err, metadata)=>{
            console.log('===>', metadata)
            updateMetadata(self, self.note.noteId, metadata)
            self.next()
          })
        }, ()=>{
          self.next(1)
        })
      })
      break
    case '@RENAME_TOPIC_FILE':
      self.assertNote(task, ()=>{
        assertUpdate(self.note.metadata, task, ()=>{
          log(' -> Topic file renamed.')
          self.note.renameFile(task.payload.topicRemoteId, task.payload.name+'.md', (err, metadata)=>{
            self.next()
          })
        }, ()=>{
          self.next(1)
        })
      })
      break

    case '@UPDATE_METADATA':
      caseUpdateMetadata(self, task)
      break // using metaId

    default:
      console.info('=> No task given')
  }
}

export default execute


