import ContentList from './ContentList'
import { connect } from 'react-redux'
import syncSave from './../../../sync_gapi/app/gapi';
import { findParent } from './../../../redux/reducers/note/util'
// import store from './../../../redux/store'

const mapStateToProps = (state) => {
  return {
    noteItems: state.note.notes,
    editElIndex: state.note.editElIndex, // content Element (not note, only sidebar content item)
    editing: state.note.editing,
    mousedown: state.note.mousedown,
    isContextMenu: state.note.showContextMenu,
    editors: state.mdEditor.editors,
    activeLinkId: state.note.activeLink,
    activeLinkIndex: state.note.activeLinkIndex,
    noteId: state.notebook.noteMeta.noteId,
    to: state.note.to,
  }
}

const mapDispatchToProps = (dispatch, action) => {
  return {
    setActiveLink: (index) => {
      dispatch({
        type: 'SET_ACTIVE_LINK',
        index
      })
    },
    setActiveEditor: (index, notes) => {
      dispatch({
        type: 'SET_ACTIVE_EDITOR',
        index,
      })
      // if (notes[index].type === 'chapter') {
      //   dispatch({
      //     type: 'INIT_EDITOR_FROM_ID',
      //     localId: notes[index].introLocalId,
      //     remoteId: notes[index].introRemoteId || '===='
      //   })
      // } else {
      //   dispatch({
      //     type: 'INIT_EDITOR_FROM_ID',
      //     localId: notes[index].localId,
      //     remoteId: notes[index].remoteId || '++++'
      //   })
      // }
    },
    setEditEl: (noteId, index) => {
      dispatch({
        type: 'SET_EDIT_EL',
        noteId,
        index,
      })
    },
    // done
    onChange: (el, name) => {
      dispatch({
        type: 'EDIT_EL_NAME',
        el: el,
        name: name
      })
    },
    
    commitElName: (editEl, props) => {
      dispatch({
        type: 'COMMIT_EL_NAME'
      })
      
      if (editEl.message) {
        console.log(editEl.message)
      }
      // id editEl.edit === false then 
      if (!editEl.edit && editEl.changed) {
        if (editEl.new) {
          console.log('Async: Saving...')
          if (editEl.type === 'chapter') {
            // fire asyncSave
            syncSave.addTask({
              type: 'CREATE_CHAPTER',
              payload: {
                name: editEl.name,
                noteId: props.noteId,
                chapterLocalId: editEl.localId,
              }
            })
          } else {
            var parentIndex = findParent(props.noteItems, editEl.gIndex)
            var parent = props.noteItems[parentIndex]
            syncSave.addTask({
              type: 'CREATE_TOPIC',
              payload: {
                name: editEl.name,
                noteId: props.noteId,
                chapterLocalId: parent.localId,
                chapterRemoteId: parent.remoteId,
                topicLocalId: editEl.localId,
              }
            })
          }
        } else {
          // JUST UPDATE THE NAME
          console.log('Updating file name')
          if (editEl.type === 'chapter') {
            syncSave.addTask({
              type: 'RENAME_CHAPTER',
              payload: {
                name: editEl.name,
                noteId: props.noteId,
                chapterLocalId: editEl.localId,
                chapterRemoteId: editEl.remoteId,
              }
            })
          } else {
            syncSave.addTask({
              type: 'RENAME_TOPIC',
              payload: {
                name: editEl.name,
                noteId: props.noteId,
                topicLocalId: editEl.localId,
                topicRemoteId: editEl.remoteId,
              }
            })
          }
        }
        dispatch({
          type: 'MAKE_LINK_OLD'
        })
        
      }
      // if editEl has remoteId then only change the name
      // else crete a remote file add remoteId to the note 
    },
    setEditing: (index, flag) => {
      dispatch({
        type: 'SET_EDITING',
        flag,
        index
      })
    },
    addTopic: (el, noteId, props) => {
      dispatch({
        type: 'ADD_NEW_TOPIC',
        index: el.gIndex,
        noteId,
      })
      // var path = `/note/edit/${noteId}/${el.gIndex}`
      // history.push(path)
    },
    onMouseDown: (el) => {
      dispatch({
        type: 'ON_MOUSE_DOWN',
        el
      })
    },
    onMouseMove: (el, event) => {
      dispatch({
        type: 'ON_MOUSE_MOVE',
        el,
        event
      })  
    },
    onMouseUp: () => {
      dispatch({
        type: 'ON_MOUSE_UP'
      })
    },
    showContextMenu: (event, targetEl) => {
      dispatch({
        type: 'SHOW_CONTENT_CONTEXT_MENU_STYLE',
        event,
        targetEl
      })
    },
    hideContextMenu: () => {
      dispatch({
        type: 'HIDE_CONTENT_CONTEXT_MENU_STYLE'
      })
    }
  }
}

const ConnectedContentList = connect(
  mapStateToProps,
  mapDispatchToProps
)(ContentList)

export default ConnectedContentList
















