import React, { Component } from 'react';
import { Route, HashRouter as Router, Switch } from 'react-router-dom';

import './styles/index.scss'

import  CompactEditor from './../su-md-editor/compact-editor.conn';
import SidebarContentEditor from './SidebarContentEditor/conn';
// import Note from './../../gapi/note';
// import AsyncNote from './../../gapi/async-note';
import handleGauthLoad from './../../gapi/gapi'

class _Note extends Component {
  componentWillReceiveProps(nextProps) {
    if (this.props.match.params.noteId !== nextProps.match.params.noteId) {
      this.initNote()
    }
  }

  componentDidMount() {
    // coming newly
    this.initNote()
    window.H = this.props.history
  }

  initNote () {
    var self = this
    var params = this.props.match.params
    console.log('initializing note...')

    if (this.props.authorized) {
      this.props.createNote({noteId: params.noteId, index: params.index, history:this.props.history})
    }

    if (window.env !== 'dev'){
      if (!this.props.authorized) {
        handleGauthLoad((err, res)=>{
          self.props.setNotebookState({
            authorized: true
          })
          // console.log('Authorized...')
          // console.log('+++', params.noteId)
          self.props.createNote({noteId: params.noteId, index: params.index, history:this.props.history})
        })
      }
    } else { // DEV
      self.props.createNote({noteId: params.noteId, index: params.index, history:this.props.history})
    }
  }
  setPreviousActive () {
    var prevElIndex = parseInt(this.props.activeEditorIndex, 10) - 1
    if (prevElIndex > -1) {
      var path = `/note/edit/${this.props.noteId}/${prevElIndex}`
      this.props.history.push(path)
    }
  }

  setNextActive () {
    var nextElIndex = parseInt(this.props.activeEditorIndex, 10) + 1
    if (nextElIndex < this.props.noteItems.length) {
      var path = `/note/edit/${this.props.noteId}/${nextElIndex}`
      this.props.history.push(path)
    }
  }

  render() {
    var self = this
    var styleSidebar = {
      width: self.props.sidebarVisible ? '300px':'0'
    }
    var styleNotes = {
      width: self.props.sidebarVisible ? 'calc(100% - 300px)':'100%',
      // marginLeft: self.props.sidebarVisible ? '300px':'',
    }

    return (
      <div className="course-notes">
        <div className="h100p">
          <div className="col sidebar-note no-plr" style={styleSidebar}>
            <div className="sidebar-note-content">
              <Router>
                <Switch>
                  <Route path={`/note/edit/:noteId/:index`} component={SidebarContentEditor}/>
                  <Route path={`/note/edit/:noteId/`} component={SidebarContentEditor}/>
                </Switch>
              </Router>
            </div>
          </div>
          <div className="col note-content no-plr" style={styleNotes}>
            {this.props.noedit ? <div className="note-editor editor-go-prev" onMouseUp={this.setPreviousActive.bind(this)}>
              <i className="go-prev mdi mdi-chevron-left"></i>
            </div> : ''}
            <div className="note-reading-space">

            { this.props.showEditor ?
              <CompactEditor
                parent={this}
                showEditor={false}
                onSave={()=>{}}
                onEdit={()=>{}}
                id={this.props.activeEditorLocalId}
                remoteId={this.props.activeEditorRemoteId}
                key={this.props.activeEditor} /> : ''
            }
            
            {this.props.noedit ? <div className="note-editor editor-go-next" onMouseUp={this.setNextActive.bind(this)}>
              <i className="go-next mdi mdi-chevron-right"></i>
            </div> : ''}
            </div>
          </div>
        </div>
      </div>
    );
  }
}



export default _Note;


