import Chance from 'chance';
// import store from './../../store'
import { addChapter, removeChapter, addTopic, removeTopic, toggleDisabled } from './note.fn'
import { onMouseMove, onMouseUp } from './note.mouse.fn'
import { chapterContextMenu, topicContextMenu } from './sidebar-context-menu.fn'
import { findDownPosition } from './util'

var chance = new Chance();
var poolStr = 'abcdefghijklmnopqrstuvwxyz0123456789'

const note = (state, action) => {
  console.log('NOTE: ', action.type, action)
  // TODO: dont create new object every time
  var obj = {
    notes: [],
    // notes: [
    //   {
    //     id: 'sadasdasdasdasdasd1',
    //     name: 'New Chapter 1',
    //     edit: false,
    //     selected: true,
    //     type: 'chapter',
    //     children: 0,
    //     gIndex: 0,
    //     disabled: false,
    //     icon: '---'
    //   },
    //   {
    //     id: 'sadasdasdasdasdasd2',
    //     name: 'New Chapter 2',
    //     edit: false,
    //     selected: false,
    //     type: 'chapter',
    //     children: 0,
    //     gIndex: 1
    //   },
    //   {
    //     id: 'sadasdasdasdasdasd3',
    //     name: 'New Chapter 3',
    //     edit: false,
    //     selected: false,
    //     type: 'chapter',
    //     children: 0,
    //     gIndex: 2
    //   }
    // ],
    activeLink: -1,
    activeLinkIndex: -1,
    activeEditorRemoteId: -1,
    activeEditorLocalId: -1,

    // sidebar content edit state
    editElIndex: 'sadasdasdasdasdasd1',//-1,
    editing: false,
    showEditor: false, // THIS IS SET WHEN EDITOR IS LOADED FROM CLOUD

    // sidebar content dragging state
    mousedown: false,
    elId: -1,
    el: '',
    dropIndex: -1,
    dropEl: '',
    elChildren: [],

    // sidebar content context menu state
    showContextMenu: false,
    contextMenuStyle: {},
    ctxTargetEl: {},
    contextItems: [],

    sidebarVisible: true,
    to: ''
  }

  var notes = {},
      editElIndex = -1,
      el = {},
      newState = {},
      contextItems = [],
      targetEl = '',
      index = -1,
      end = -1,
      path = '',
      activeEditorLocalId = '',
      activeEditorRemoteId = '';

  switch(action.type){
    case 'UPDATE_REMOTE_ID':
      notes = state.notes.map((el)=>{
        if (el.localId === action.localId) {
          el.remoteId = action.remoteId
        }
        if (el.parentLocalId === action.localId) {
          el.parentId = action.remoteId
        }
        if (el.introLocalId === action.localId) {
          el.introRemoteId = action.remoteId
        }
        return el
      })
      return Object.assign({}, state, {
        notes 
      })
      
    case 'SET_ACTIVE_EDITOR':
      el =  state.notes[action.index] 
      activeEditorRemoteId = el.remoteId
      if (el.type === 'chapter') {
        activeEditorRemoteId = el.introRemoteId
        activeEditorLocalId = el.introLocalId
      } else {
        activeEditorRemoteId = el.remoteId
        activeEditorLocalId = el.localId
      }
      if (el) {
        return Object.assign({}, state, {
          activeLink: el.localId,
          activeLinkIndex: action.index,
          activeEditorRemoteId,
          activeEditorLocalId,
          showEditor: true,
        })
      } else {
        return state
      }

    case 'SET_ACTIVE_LINK':
      el =  state.notes[action.index] 
      if (el) {
        notes = state.notes.map((_el, _index) => {
          _el.selected = (_index === el.gIndex)
          return _el
        })

        return Object.assign({}, state, {
          notes,
          activeLinkIndex: action.index,
        })
      } else {
        return state
      }

    case 'SET_EDIT_EL':
      notes = [].concat(state.notes)
      if (action.index !== -1) {
        notes[action.index].prevName = notes[action.index].name
        notes[action.index].changed = false
      }
      path = `/note/edit/${action.noteId}/${action.index}`
      return Object.assign({}, state, {
        editElIndex: action.index,
        notes,
        to: path
      })

    case 'SET_EDITING':
      notes = [].concat(state.notes)
      notes[action.index].edit = true
      return Object.assign({}, state, {
        editing: action.flag,
        notes
      })

    case 'EDIT_EL_NAME':
      notes = state.notes.map((_el) => { 
        _el.selected = false
        return _el
      })
      notes[state.editElIndex].name = action.name
      notes[state.editElIndex].changed = true
      return Object.assign({}, state, { notes })
      
    case 'COMMIT_EL_NAME':
      // if name already exists do not commit
      index = 0
      notes = [].concat(state.notes)
      el = notes[state.editElIndex]
      el.name = el.name.trim(' ')
      
      notes.forEach((_el, _index)=>{
        if (el.type === 'chapter') {
          if (_el.type === 'chapter') {
            if(_el.name === el.name && _index !== state.editElIndex) {
              index = 1
            }
          }
        } else {
          if (_el.type === 'topic') {
            if(_el.name === el.name && _index !== state.editElIndex && el.parentId === _el.parentId) {
              index = 1
            }
          }
        }
      });
      if (index) {
        el.message = 'This name already exists!'
        return Object.assign({}, state, { notes })
      }
      el.message = ''
      el.selected = true
      el.edit = false
      if (el.name !== el.prevName) {
        el.changed = true
      }
      return Object.assign({}, state, { notes, editing: false })

    // This helps to avoid creation of new file during name change commit
    case 'MAKE_LINK_OLD':
      notes = [].concat(state.notes)
      el = notes[state.editElIndex]
      el.new = false
      return Object.assign({}, state, { notes })

    case 'ADD_NEW_CHAPTER':
      if (state.editing) {
        return state
      }
      var newChapterId = chance.string({length: 32, pool: poolStr})
      var newIntroId = chance.string({length: 32, pool: poolStr})
      notes = addChapter(state.notes, newChapterId, newIntroId)

      editElIndex = notes.length - 1
      el = notes[editElIndex]
      // store.dispatch
      path = `/note/edit/${action.noteId}/${editElIndex}`

      return Object.assign({}, state, {
        notes,                                 // update notes
        editElIndex,                          // used in ContentList.onBlur
        editing: true,                       // make the editor change the mode
        showEditor: true,
        mousedown: false,
        activeLink: newChapterId,
        activeLinkIndex: editElIndex,
        activeEditorLocalId: el.introLocalId,
        activeEditorRemoteId: el.introRemoteId,
        to: path
      })

    case 'ADD_NEW_TOPIC':
      var newTopicId = chance.string({length: 32, pool: poolStr});
      obj = addTopic(state.notes, newTopicId, action.index)

      path = `/note/edit/${action.noteId}/${obj.addTopicIndex}`
      if (state.editing) {
        return state
      }
      return Object.assign({}, state, {
        notes: [].concat(obj.notes),              // update notes
        editElIndex: obj.addTopicIndex,          // used in ContentList.onBlur
        editing: true,                          // make the editor change the mode
        edit: true,
        mousedown: false,
        showEditor: true,
        activeLink: newTopicId, 
        activeLinkIndex: obj.addTopicIndex,
        activeEditorLocalId: newTopicId,
        activeEditorRemoteId: undefined,
        to: path
      })

    case 'REMOVE_TO_PATH':
      return Object.assign({}, state, {
        to: ''
      })

    case 'ON_MOUSE_DOWN':
      return Object.assign({}, state, {
        elId: action.el.localId,
        mousedown: true
      })

    case 'ON_MOUSE_MOVE':
      newState = onMouseMove(state, action.el, action.event)
      return newState

    case 'ON_MOUSE_UP':
      newState = onMouseUp(state)
      return Object.assign({}, newState)

    case 'HIDE_CONTENT_CONTEXT_MENU_STYLE':
      return Object.assign({}, state, {
        showContextMenu: false
      })

    case 'SHOW_CONTENT_CONTEXT_MENU_STYLE':
      // console.log(action.targetEl)
      // process action.targetEl and create appropriate context menu
      targetEl = action.targetEl
      if (targetEl.type === 'chapter') {
        contextItems = chapterContextMenu
      } else {
        contextItems = topicContextMenu
      }

      contextItems[1].nameIndex = targetEl.disabled ? 1:0     // for make disabled/enabled
      contextItems[3].nameIndex = targetEl.icon ? 1:0        // for add icon/ remove icon

      return Object.assign({}, state, {
        contextItems,
        ctxTargetEl: action.targetEl,
        showContextMenu: true,
        contextMenuStyle: {
          left: action.event.nativeEvent.clientX + 'px',
          top: action.event.nativeEvent.clientY + 'px'
        }
      })

    case 'TOGGLE_DISABLED':
      notes = toggleDisabled(state.notes, state.ctxTargetEl.gIndex)
      return Object.assign({}, state, {
        notes,
        showContextMenu: false  
      })

    case 'REMOVE_TOPIC':
      notes = removeTopic(state.notes, state.ctxTargetEl.gIndex)
      
      return Object.assign({}, state, { 
        notes, 
        showEditor: notes.length,
        showContextMenu: false
      })

    case 'REMOVE_CHAPTER':
      notes = removeChapter(state.notes, state.ctxTargetEl.gIndex, state.ctxTargetEl.children)
      return Object.assign({}, state, { 
        notes,
        showEditor: notes.length,
        showContextMenu: false  
      })

    case 'TOGGLE_SIDEBAR':
      return Object.assign({}, state, { 
        sidebarVisible: !state.sidebarVisible
      })

    case 'CHAPTER_SET_REMOTE_ID':
      index = action.index
      notes = [].concat(state.notes)
      end = findDownPosition(notes, index)
      for(var j = index+1, length2 = end; j < length2; j++){
        notes[j].parentId = action.localId
      }
      return Object.assign({}, state, { 
        notes
      })

    case 'NOTE_SET_STATE':
      return Object.assign({}, state, action.payload)

    default:
      return state || obj
  }
}

export default note;


