import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import './index.scss'

class LoginPage extends Component {
  trySignIn () {
    console.log('Trying to login...')
    this.props.history.push('/notebooks')
  }

  render() {
    return (
      <div className="login-page-container">
        <div className="login-page layout row">
          <div className="login-page-info col md-6 md-o-3 lg-4 lg-o-4">
            Sign in to noteapp
          </div>
          <div className="col md-6 md-o-3 lg-4 lg-o-4">
            <form action="#" className="main-form">
              <input type="text" className="form-input" placeholder="username or email address"  onChange={()=>{}}/>
              <input type="password" className="form-input" placeholder="password" onChange={()=>{}}/>
              <button className="sign--btn w100p" onMouseUp={this.trySignIn.bind(this)}>
                Sign in
              </button>
            </form>
          </div>
          <div className="login-page-signup col md-6 md-o-3 lg-4 lg-o-4">
            <div className="new-signup">
              Don't have a account? <Link to="/"> Sign up </Link>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default LoginPage


