import React, { Component } from 'react';
import './index.scss';

// import AsyncNote from './../../../gapi/async-note';
// import async from './../../gapi/async'

 class ContentList extends Component {
  _onMouseMoveInput (e) {
    e.stopPropagation()
  }

  _onLinkClick (e, el) {
    this.activateLink(el)
  }

  activateLink (el) {
    this.props.setActiveNote(el.id, el.gIndex)
    this.props.setActiveLink(el.id)
  }

  /**
   * acivate link on select
   * creates the content list sidebar content
   */
  render () {
    var className = ''
    // var addEl = (this.props.chapters) ? <span className="c-ctrl add-topic">+</span> : ''
    var listEl = this.props.noteItems.map((el, index)=>{
      if ((el.type === 'chapter' && el.children === 0) || el.type === 'topic') {
        var liTopic = ''
        if (el.type === 'topic') {
          liTopic = 'li-topic'
        }

        className = 'note-link-topic '
        className += el.disabled ? 'disabled ':''
        className += el.selected ? 'active':''

        return <li className={"simple-li " + liTopic + " el-" + el.id} key={index}
          onMouseDown={(e)=>{this._onLinkClick(e, el)}}>
          <span className={className}>
            {el.name}
          </span>
        </li>
      } else {
        className = 'note-link-chapter bold '
        if(el.disabled){
          className += 'disabled '
        }
        if(el.selected){
          className += 'active'
        }

        return <li className={"simple-li el-" + el.id} key={index}
          onMouseDown={(e)=>{this._onLinkClick(e, el)}}>
          <span className={className}>
            {el.name}
          </span>
        </li>
      }
    })

    return (
      <ul className="simple-ul no-bold">
        {listEl}
      </ul>
    )
  }
}

export default ContentList;


