import { consistentgIndex, findDownPosition, findParent } from './util';

const addChapter = (_notes, _id, _introId) => {
  var notes = _notes.map((_el) => {
    _el.selected = false
    return _el;
  })

  notes = notes.concat([{
    localId: _id,
    name: 'New Chapter',
    introLocalId: _introId,
    edit: true,
    selected: true,
    type: 'chapter',
    children: 0,
    new: true,
  }])
  consistentgIndex(notes)
  return notes;
}

const removeChapter = (_notes, _index, _count) => {
  var notes = _notes.map((_el) => {
    _el.selected = false
    return _el;
  })
  notes = notes.slice(0, _index).concat(notes.slice(_index + _count + 1))
  consistentgIndex(notes)
  return notes;
}

const addTopic = (_notes, _id, _index) => {
  var notes = _notes.map((_el) => {
    _el.selected = false
    return _el;
  })
  var el = notes[_index]

  var addTopicIndex = findDownPosition(_notes, _index)

  var newTopic = Object.assign({
    localId: _id,
    name: 'New Topic',
    edit: true,
    selected: true,
    type: 'topic',
    parentId: el.remoteId || el.id, // used when server sync
    new: true,
  })

  el.children++
  notes.splice(addTopicIndex, 0, newTopic)
  consistentgIndex(notes)

  // this.props.editEl = addTopicIndex
  // this.editing = true

  return {notes, addTopicIndex}
}

const removeTopic = (_notes, index) => {
  var notes = _notes.map((_el) => {
    _el.selected = false
    return _el;
  })
  var parentIndex = findParent(notes, index)
  notes[parentIndex].children--
  notes = notes.slice(0, index).concat(notes.slice(index+1))
  consistentgIndex(notes)
  return notes;
}

const toggleDisabled = (_notes, _index) => {
  _notes[_index].disabled = !_notes[_index].disabled
  return _notes.slice(0, _index)
          .concat([_notes[_index]])
          .concat(_notes.slice(_index+1))
}

export {
  addTopic,
  removeTopic,
  addChapter,
  removeChapter,
  toggleDisabled
}


