/* eslint-disable no-alert, no-console */
import localStore from 'store'
/* eslint-enable no-alert, no-console */
import { log, logStore } from './../utils'
import store from '../../../../redux/store'

// eslint-disable-next-line
let remoteToLocalMap = remoteToLocalMap || localStore.get('remote-local-id-map') || {}

const updateFutureTask = function (self, cb) {
  for (var i = 0; i < self.tasks.length; i++) {
    cb(self.tasks[i])
  }
  // update localStorage
  localStore.set('tasks', self.tasks)
}

const updateChapterRemoteId = function (self, localId, remoteId) {
  updateFutureTask(self, (task)=>{
    log(localId, remoteId)
    if (task.payload.chapterLocalId === localId) {
      task.payload.chapterRemoteId = remoteId
      remoteToLocalMap[remoteId] = localId
      localStore.set('remote-local-id-map', remoteToLocalMap)
      logStore.push(task.type)

      // dispatch for store remoteId update
      store.dispatch({
        type: 'UPDATE_REMOTE_ID',
        remoteId,
        localId,
      })      
    }
  })
}

const updateTopicRemoteId = function (self, localId, remoteId) {
  updateFutureTask(self, (task)=>{
    if (task.payload.topicLocalId === localId) {
      task.payload.topicRemoteId = remoteId
      remoteToLocalMap[remoteId] = localId
      localStore.set('remote-local-id-map', remoteToLocalMap)
      logStore.push(task.type)

      // dispatch for store remoteId update
      store.dispatch({
        type: 'UPDATE_REMOTE_ID',
        remoteId,
        localId,
      })      
    }
  });
}

const updateMetadata = function (self, noteId, metadata) {
  console.log(metadata)
  log([].concat(self.tasks))
  updateFutureTask(self, (task)=>{
    if (task.type === '@UPDATE_METADATA') {
      log(noteId, task, noteId === task.payload.noteId)
      if (task.payload.noteId === noteId) {
        log('Updating future metadata....')
        task.payload.metadata = metadata
        logStore.push('Update metadata => ',task.type)
      }
    }
  });
}

export {
  updateMetadata,
  updateTopicRemoteId,
  updateChapterRemoteId,
}


