import Note from './su-note-reader'
import { connect } from 'react-redux'

const mapStateToProps = (state) => {
  return {
    sidebarVisible: state.noteReader.sidebarVisible,
    activeNote: state.noteReader.activeNote,
    htmlText: state.noteReader.htmlText,
  }
}

const mapDispatchToProps = (dispatch, action) => {
  return {
    setPreviousActive: () => {
      dispatch({
        type: 'R_SET_PREVIOUS_ACTIVE'
      })
    },
    setNextActive: () => {
      dispatch({
        type: 'R_SET_NEXT_ACTIVE'
      })
    },
    toggleSidebar: () => {
      dispatch({
        type: 'R_TOGGLE_SIDEBAR'
      })
    }
  }
}

const VisibleNote = connect(
  mapStateToProps,
  mapDispatchToProps
)(Note)

export default VisibleNote
















