/* eslint-disable no-alert, no-console */
import localStore from 'store'
/* eslint-enable no-alert, no-console */

const addTask = function (self, task){
    localStore.set('updateRemaining', true)
    task.payload.timestamp = self.timeStamp()
    var _task = Object.assign({}, task)
      switch (task.type) {
        case 'CREATE_CHAPTER':
          // create extrea jobs
          self.tasks.push({
            type: '@CREATE_CHAPTER_FILE',
            payload: task.payload
          })

          self.tasks.push({
            type: '@CREATE_CHAPTER_INTRO_FILE',
            payload: task.payload
          })

          task.payload.metadata = ''
          self.tasks.push({
            type: '@UPDATE_METADATA',
            payload: task.payload
          })
          break;

        case 'CREATE_TOPIC':
          // create extrea jobs
          self.tasks.push({
            type: '@CREATE_TOPIC_FILE',
            payload: task.payload
          })
          task.payload.metadata = ''
          self.tasks.push({
            type: '@UPDATE_METADATA',
            payload: task.payload
          })
          break;

        case 'DELETE_TOPIC':
          self.tasks.push({
            type: '@DELETE_TOPIC_CREATE_METADATA',
            payload: task.payload
          })

          self.tasks.push({
            type: '@UPDATE_METADATA',
            payload: task.payload
            // metadata will be attatched 
          })

          self.tasks.push({
            type: '@DELETE_TOPIC_FILE',
            payload: task.payload
          })
          break

        case 'DELETE_CHAPTER':
          self.tasks.push({
            type: '@DELETE_CHAPTER_CREATE_METADATA',
            payload: task.payload
          })
          task.payload.metadata = ''
          self.tasks.push({
            type: '@UPDATE_METADATA',
            payload: task.payload
            // metadata will be attatched 
          })

          self.tasks.push({
            type: '@DELETE_CHAPTER_FILE',
            payload: task.payload              
          })
          break

        case 'UPDATE_CHAPTER_INTRO_FILE':
          task.type = '@'+task.type
          self.tasks.push(task)
          task.payload.metadata = ''
          self.tasks.push({
            type: '@UPDATE_METADATA',
            payload: task.payload
          })
          break

        case 'UPDATE_TOPIC_DATA':
          task.type = '@UPDATE_TOPIC_FILE'
          self.tasks.push(task)
          task.payload.metadata = ''
          self.tasks.push({
            type: '@UPDATE_METADATA',
            payload: task.payload
          })
          break
        
        case 'UPDATE_CHAPTER':
          task.type = '@UPDATE_TOPIC_FILE'
          self.tasks.push(task)
          task.payload.metadata = ''
          self.tasks.push({
            type: '@UPDATE_METADATA',
            payload: task.payload
          })
          break

        case 'RENAME_TOPIC':
          task.type = '@RENAME_TOPIC_CREATE_METADATA'
          self.tasks.push(task)
          task.payload.metadata = ''
          self.tasks.push({
            type: '@UPDATE_METADATA',
            payload: task.payload
          })
          self.tasks.push({
            type: '@RENAME_TOPIC_FILE',
            payload: task.payload
          })
          break

        case 'RENAME_CHAPTER':
          task.type = '@RENAME_CHAPTER_CREATE_METADATA'
          self.tasks.push(task)
          task.payload.metadata = ''
          self.tasks.push({
            type: '@UPDATE_METADATA',
            payload: task.payload
          })
          self.tasks.push({
            type: '@RENAME_CHAPTER_FILE',
            payload: task.payload
          })
          break

        default:
          if (_task.type[0] !== '@') {
            _task.type = '@'+task.type
          }
          self.tasks.push(_task)
          // update localStorage
          localStore.set('tasks', self.tasks)
          break;
    }

    if (!self.running) {
      self.run()
    }
  // setTimeout(()=>{
  // }, 100)
}

export default addTask


