import React, { Component } from 'react';

import './styles/index.scss'
import './styles/md.scss'

import ContentList from './ContentList/conn'

class _Note extends Component {
  constructor (props) {
    super(props)
    console.log('--')
      // craete new Note
      // this.props.gotTo('')
      // if params.contentName == undefined 
        // redirect to inintial paramnot topic
      // AsyncNote.note = new Note(this.props.match.params.noteId)

    // parseEditorNo(this.state.children, this.state.noteEditors)
  }

  componentWillReceiveProps(nextProps) {
    console.log('1', nextProps.match)
    // load new content
    // if (this.props.match.params.noteId) {
    //   console.log(this.props.match.params.noteId)
    //   console.log(this.props.match.params)
    //   if (this.props.match.params.contentName) {}
    // }
  }

  afterCompileProcess () {
    // highlight the codes
    var code = this.refs.htmlText.querySelectorAll('pre code')
    code.forEach(function (el, index) {
      window.hljs.highlightBlock(el)
    })
  }

  componentDidUpdate(prevProps, prevState) {
    this.afterCompileProcess()
  }
  componentDidMount() {
    this.afterCompileProcess()
    console.log('2', this.props.match)
    console.log('3', this.props.match.contentName)
  }

  render() {
    var self = this
    var styleSidebar = {
      width: self.props.sidebarVisible ? '250px':'0px'
    }
    var styleNotes = {
      width: self.props.sidebarVisible ? 'calc(100% - 250px)': '100%',//'calc(100% - 250px)',
      marginLeft: self.props.sidebarVisible ? '250px':'',
    }
    // var styleRightSidebar = {
    //   width: !self.props.sidebarVisible ? '250px':'0'
    // }
    var readStyle = {
      padding: self.props.sidebarVisible ? '0 100px':'0 140px'
    }

    return (
      <div className="read-course-notes">
        <div className="h100p">
          <div className="col read-sidebar-note no-plr" style={styleSidebar}>
            <div className="read-sidebar-note-content">
              <ContentList/>
            </div>
          </div>
          <div className="col read-note-content no-plr" style={styleNotes}>

            <div className="read-note-reader reader-go-prev" onMouseUp={this.props.setPreviousActive}>
              <i className="go-prev mdi mdi-chevron-left"></i>
            </div>
            <div className="read-note-reading-space">
              <div className="note-reader-toolbar">
                {this.props.sidebarVisible ? <i className="go-prev mdi mdi-chevron-left"
                  onMouseUp={this.props.toggleSidebar}></i> :''}
                {!this.props.sidebarVisible ? <i className="go-prev mdi mdi-chevron-right"
                  onMouseUp={this.props.toggleSidebar}></i> :''}
              </div>
              <div className="p-lr-80" style={readStyle}>
                <div ref="htmlText" style={{margin: '0 auto'}} dangerouslySetInnerHTML={{ __html: this.props.htmlText }}></div>
              </div>
            </div>
            <div className="read-note-reader reader-go-next" onMouseUp={this.props.setNextActive}>
              <i className="go-next mdi mdi-chevron-right"></i>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
          // <div className="col sidebar-note-right  no-plr" style={styleRightSidebar}>
          // </div>

export default _Note;


