import store from './../../store'
import NoteBook from './../../../gapi/notebook'
import Note from './../../../gapi/note'

// eslint-disable-next-line 
let suNote = suNote || {}
// eslint-disable-next-line 
let suNoteBook = suNoteBook || {}

window.NoteBook = NoteBook
window.Note = Note

const note = (state, action) => {
  // TODO: dont create new object every time
  var obj = {
    noteBookMeta: {},
    noteMeta: {},
    noNoteExists: false,
    authorized: false
  }
  var noteBook = {},
      payload = {},
      editEl = {},
      remoteId = '',
      index = 0,
      chapters = [];

  switch(action.type){
    case 'CREATE_NOTEBOOK':
      noteBook = new NoteBook(action.id)
      noteBook.init((err, metadata)=>{
        if (err) {
          console.error(err)
          return
        }
        suNoteBook =  noteBook
        payload = {
          noteBookMeta: metadata
        }
        store.dispatch({
          type: 'NOTEBOOK_SET_STATE',
          payload
        })
      })
      return Object.assign({}, state, {})

    case 'CREATE_NOTE':
      if (window.env !== 'dev') {
        if (state.noteMeta.noteId === action.id) {
          console.log('alaska!')
          return state
        }
        
        suNote = new Note(action.id)
        // console.log(action.id)
        suNote.init && suNote.init((err, metadata)=>{
          if (err || (metadata.error && metadata.error.code)) {
            payload = {
              noNoteExists: true
            }
            console.error('Error:notebook: Note does not exists.', err)

            store.dispatch({
              type: 'NOTEBOOK_SET_STATE',
              payload
            })
            return
          }

          metadata.noteId = action.id
          // console.log(metadata)

          payload = {
            noteMeta: metadata
          }
          
          store.dispatch({
            type: 'NOTEBOOK_SET_STATE',
            payload
          })
        })
      }

      return Object.assign({}, state, {})

    case 'NOTEBOOK_SET_STATE':
      return Object.assign({}, state, action.payload)

    case '@SERVER_REMOVE_CHAPTER':
      if (window.env !== 'dev') {
        editEl = action.el
        suNote.deleteChapter(editEl.remoteId, (err, res)=>{
          console.log(res)
        })
      }      
      return state

    case '@SERVER_REMOVE_TOPIC':
      if (window.env !== 'dev') {
        editEl = action.el
        console.log('===>', editEl)
        suNote.deleteTopic(editEl.remoteId, (err, res)=>{
          console.log(res)
        })
      }      
      return state

    case '@SERVER_COMMIT_EL_NAME':
      editEl = action.editEl
      
      console.log(editEl, suNote.noteId)

      if (window.env !== 'dev') { 
        if (editEl.remoteId) {
        } else {
          if (editEl.type === 'topic') {
            suNote.createTopic(editEl.parentId, editEl.name, (err, res)=>{
              console.log(res)
            })
          } else if (editEl.type === 'chapter') {
            suNote.createChapter(editEl.name, (err, res)=>{
              if (err) {
                return
              }
              console.log(res)
              store.dispatch({
                type: 'CHAPTER_SET_REMOTE_ID',
                id: res.id,
                index: editEl.gIndex
              })
            })
          }
        }
      } // gapi

      return Object.assign({}, state, {})

    case '@SERVER_LOAD_NOTE_DATA':
      if (window.env !== 'dev') {
        console.log('Loading note data...', action.remoteId)
        console.log(suNote.metadata)
        // if remoteId is of chpater update Chapter/introduction file
        chapters = suNote.metadata.chapters || []
        index = -1
        remoteId = action.remoteId
        for(var j = 0, length2 = chapters.length; j < length2; j++){
          if (chapters[j].remoteId === action.remoteId) {
            index = j
            break
          }
        }
   
        if(index !== -1 && !chapters[index].disabled) {
          remoteId = chapters[index].introId
        }

        // remoteId = '0B5nfwpxzN1PHWDJOUUhMdlZYekE'

        console.log(remoteId)

        suNote.getTopicData(remoteId, (err, data, dataLen)=>{
          // compile the markdownText
          console.log(data, dataLen)
          store.dispatch({
            type: 'SET_EDITOR_STATE',
            payload: {
              markdownText: data,
              htmlText: data
            }
          })
        })
      }
      return state

    case '@SERVER_SAVE_NOTE_CONTENT':
      console.log('Saving note data...',action.remoteId, action.markdownText)
      suNote.updateTopic(action.remoteId, action.markdownText, (err, res)=>{
        // compile the markdownText
        console.log('Note data updated...')
      })
      // updating
      // updated
      return state

    default:
      return state || obj
  }
}

export default note;


