import mdCompiler from './../../../util/md-compiler'
import store from './../../store'

import axios from 'axios'

const compileAndSet = (_notes, _index) => {
  var note = _notes[_index]
  return new Promise ((resolve, reject) => {
    var htmlText = note.htmlText
    if (!htmlText) {
      // Depending on the data type compile
      // 1. if url exists load from url
      if (!note.mdUrl) {
        axios.get('https://raw.githubusercontent.com/uNetworking/uWebSockets/master/README.md')
        .then(function (res) {
          htmlText = note.htmlText = mdCompiler(res.data)
          console.log('loading')
          resolve({notes: _notes, htmlText})
        })
      } else if (note.htmllUrl) {
        axios.get('')
        .then(function (res) {
          htmlText = res.data
          resolve({notes: _notes, htmlText})
        })
      } else {
        // 2. load from the drive
        reject({notes: _notes, htmlText: '<h1>Not found</h1>'})
      }
    } else {
      resolve({notes: _notes, htmlText})
    }
  })
}

const getTime = () => {
  return (new Date()).getTime()
}

const loadAndSet = (notes, gIndex) => {
  var timer = getTime();
  compileAndSet(notes, gIndex)
  .then((payload)=>{
    if (getTime() - timer >300) {
      store.dispatch({type: 'R_SET_READER_STATE', payload})
    } else {
      setTimeout(()=>{
        store.dispatch({type: 'R_SET_READER_STATE', payload})
      }, 300 - (getTime() - timer))
    }
  }).then((payload)=>{
    store.dispatch({type: 'R_SET_READER_STATE', payload})
  })

}
export default loadAndSet


