import {EXISTS} from'./../constants'
import  gutil from'./../gcode/gutil'
import  initChapters from'./init-chapters'

//var newNote =  new Note('0B5nfwpxzN1PHazZ4MTFCX0U1OVk')
function Note (id) {
  this.noteId = id;

  this.metadata = {}
  this.noteMetaId = ''
  // create a note.json if not already exists
}

var noteProto = Note.prototype;

noteProto.init = function (cb) {
  var self = this

  // DEV ---------------------------
  if (window.env === 'dev') {
    var metadata = initChapters
    metadata.noteId = self.noteId
    self.metadata = metadata
    setTimeout(()=>{
      cb(null, self.metadata)
      // CNote.init((res)=>{
      //   cb(null, res)
      // })
    }, 1000)
    return 
  }
  // DEV END -----------------------

  // keep all task in sync otherwise theere will be inconsistencies bw local and remote
  gutil.createFileIfNotExists('note.json', self.noteId, function (err, res) {
    gutil.downloadAndDecode(res.id, function (err, noteMeta, len) {
      self.noteMetaId = res.id;
      console.log(len)
      if (len === 0) {
        self.metadata = {
          updatedAt: ((new Date()).getTime()) - 1000000,
          metaId: res.id,
          noteId: self.noteId,
          chapters: []
        }
      } else {
        // Something have been saved previously so parseit 
        self.metadata = JSON.parse(noteMeta)
        if (!self.metadata.chapters) {
          self.metadata = JSON.parse(noteMeta)
          self.metadata.chapters = []
          self.metadata.metaId = res.id
        }
      }

      if (!self.metadata.updatedAt) {
        cb('Error: Note updatedAt prop not found!')
      }

      console.log(noteMeta)

      //  else if (noteMeta.chapters.) {
      //   self.metadata.metaId = res.id;

      //   if (!self.metadata.chapters) {
      //     self.metadata.chapters = []
      //   }
      // }else{
      //   self.metadata = JSON.parse(noteMeta);
      //   if (!self.metadata.chapters) {
      //     self.metadata.chapters = []
      //   }
      // }

      if (err) {
        cb(err, self.metadata)
        return
      }

      cb(null, self.metadata)
      // show all structure
      // start the noteApp
    })
  })
}

noteProto.getChapters = function (cb) {
  // get from metadata
  cb(this.metadata.chapters)
}

noteProto.getTopics = function (chapterId, cb) {
  // update metadata
  // upload the note.json
  for(var i = 0, length1 = this.metadata.chapters.length; i < length1; i++){
    var chapter = this.metadata.chapters[i];
    if (chapter.id === chapterId) {
      cb(chapter.topics)
      return
    }
  }
}

noteProto.createChapterSync = function (name, cb) {
  // DEV ---------------------------
  if (window.env === 'dev') {
    // CNote.createChapter(name, function (id) {
    //   cb(null, {id})
    // })
    setTimeout(()=>{
      cb(null, {id: Math.floor(Math.random()*1000)})
    }, 500)
    return
  }
  // update metadata
  // upload the note.json
  for(var i = 0, length1 = this.metadata.chapters.length; i < length1; i++){
    var chapter = this.metadata.chapters[i];
    if (chapter.name === (name + '.chapter')) {
      console.log('chapter already exists');
      cb(chapter, EXISTS)
      return
    }
  }

  gutil.createDirWithExt(this.noteId, name, '.chapter', function (err, res) {
    if (err) {cb(err); return}
    cb(null, res)
  });
}

noteProto.createChapterIntroSync = function (id, name, cb) {
  var self = this
  if (window.env === 'dev') {
    // DEV:COMM: create localStore intro.md if not already exists
    
    // self.metadata.chapters.forEach((chapter)=>{
    //   if (chapter.id === id) {
    //     if (chapter.introId === res.id) {
    //       setTimeout(()=>{
    //         cb(null, self.metadata)
    //       }, 1000)
    //       return
    //     }
    //   }
    // });

    var introId =  Math.floor(Math.random()*1000)
    self.metadata.chapters.push({
      name: name,
      id: id,
      introId,
      topics: []
    })
    setTimeout(()=>{
      cb(null, self.metadata, introId)
    }, 1000)
    return
  }
  // create introduction file
  gutil.createFileIfNotExists('intro.md', id, function (err, res) {
    // save the metadata

    // if intro.md created but not saved in metadata
    self.metadata.chapters.forEach((chapter)=>{
      if (chapter.id === id) {
        if (chapter.introId === res.id) {
          cb(null, self.metadata, res.id)
          return
        }
      }
    });

    self.metadata.chapters.push({
      name: name,
      id: id,
      introId: res.id,
      topics: []
    })
    console.log(self.metadata)
    cb(null, self.metadata, res.id) // saved afterwards
    // save the note metadata
    // self.updateNoteMetadata(cb, );
  });
}

noteProto.createChapter = function (name, cb) {
  // update metadata
  // upload the note.json
  var self = this;
  for(var i = 0, length1 = this.metadata.chapters.length; i < length1; i++){
    var chapter = this.metadata.chapters[i];
    if (chapter.name === (name + '.chapter')) {
      console.log('chapter already exists');
      cb(chapter, EXISTS)
      return
    }
  }

  gutil.createDirWithExt(this.noteId, name, '.chapter', function (err, res1) {
    if (err) {cb(err); return}
    
    // create introduction file
    gutil.createFileIfNotExists('intro.md', res1.id, function (err, res2) {
      // save the metadata
      self.metadata.chapters.push({
        name: res1.name,
        id: res1.id,
        introId: res2.id,
        topics: []
      })
      // save the note metadata
      self.updateNoteMetadata(cb, {chapter: res1, intro: res2});
    });

  });
}

noteProto.createTopic = function (chapterId, name, cb) {
  var chapterIndex = -1;
  var self = this;
  for(var i = 0, length1 = this.metadata.chapters.length; i < length1; i++){
    var chapter = this.metadata.chapters[i];
    if (chapter.id === chapterId) { // found chapter
      chapterIndex = i;
      for(var j = 0, length2 = chapter.topics.length; j < length2; j++){
        var topic = chapter.topics[j];
        if (topic.name === name) { // found topic
          console.log('topic already exists');
          cb(chapter, EXISTS)
          return
        }
      }

      break
    }
  }

  if (chapterIndex === -1) {return}

  // create the chapter
  gutil.createFileIfNotExists(name+'.md', chapterId, function (err, res) {
    // save the metadata
    var chapter = self.metadata.chapters[chapterIndex]
    chapter.topics.push({
      name: res.name,
      id: res.id,
    })

    // save the metadata
    self.updateNoteMetadata(cb, {topic: res});
  });
}

noteProto.createTopicSync = function (chapterId, name, cb) {
    var self = this;
    var chapterIndex = -1;
    for(var i = 0, length1 = this.metadata.chapters.length; i < length1; i++){
      var chapter = this.metadata.chapters[i];
      if (chapter.id === chapterId) { // found chapter
        chapterIndex = i;
        for(var j = 0, length2 = chapter.topics.length; j < length2; j++){
          var topic = chapter.topics[j];
          if (topic.name === name) { // found topic
            console.log('topic already exists');
            cb(null, self.metadata, topic.id)
            return
          }
        }

        break
      }
    }

    // DEV ---------------------------
    if (window.env === 'dev') {
      setTimeout(()=>{
        var chapter = self.metadata.chapters[chapterIndex]
        var id = Math.floor(Math.random()*1000)
        chapter.topics.push({
          name: name,
          id: id,
        })
        cb(null, self.metadata, id)
      }, 1000)
      return
    }

    if (chapterIndex === -1) {return}

    // create the chapter
    gutil.createFileIfNotExists(name+'.md', chapterId, function (err, res) {
      // save the metadata
      var chapter = self.metadata.chapters[chapterIndex]
      chapter.topics.push({
        name: res.name,
        id: res.id,
      })

    cb(null, self.metadata, res.id)
    // save the metadata
    // self.updateNoteMetadata(cb, {topic: res});
  });
}

noteProto.getTopicData = function (topicId, cb) {
  if (window.env === 'dev') {
    cb(null, 'Loaded from dev getTopicData for remoteId => '+ topicId)
    return
  }
  gutil.downloadAndDecode(topicId, cb);
}

noteProto.renameChapter = function (chapterId, name, cb) {
  if (window.env === 'dev') {
    // cb(null, 'Loaded from dev getTopicData for remoteId => '+ topicId)
    return
  }
  for(var i = 0, length1 = this.metadata.chapters.length; i < length1; i++){
    var chapter = this.metadata.chapters[i];
    if (chapter.id === chapterId) { // found chapter
      chapter.name = name
      break
    }
  }
  cb(null, this.metadata)
}

noteProto.renameTopic = function (topicId, name, cb) {
  for(var i = 0, length1 = this.metadata.chapters.length; i < length1; i++){
    var chapter = this.metadata.chapters[i];
    for(var j = 0, length2 = chapter.topics.length; j < length2; j++){
      var topic = chapter.topics[j];
      if (topic.id === topicId) { // found topic
        topic.name = name
        break
      }
    }
  }
  if (window.env === 'dev') {
    cb(null, this.metadata)
    return
  }
  cb(null, this.metadata)
}

noteProto.renameFile = function (id, name, cb) {
  if (window.env === 'dev') {
    cb(null, 'Done from dev')
    return
  }
  gutil.renameFile(id, name, cb)
}

noteProto.updateTopic = function (id, data, cb) {
  gutil.uploadFile(id, '/', data, cb);
}

noteProto.deleteChapter = function (id, cb) {
  var flag = 0;
  // find chapter
  for(var k = 0, length3 = this.metadata.chapters.length; k < length3; k++){
    var chapter = this.metadata.chapters[k];
    if (chapter.id  === id) {
      //matched id 
      this.metadata.chapters.splice(k, 1);
      flag = 1
      break
    }
  }
  if (flag) {
    this.updateNoteMetadata((res)=>{
      // delete the topic-name.md file
      gutil.deleteFile(id, cb);
    })
  }
}

/**
 * 1. deleteChapterSync
 * 2. updateMetadata
 * 3. deleteFile
 */

noteProto.deleteChapterSync = function (id, cb) {
  var flag = 0;
  var self = this
  // find chapter
  for(var k = 0, length3 = this.metadata.chapters.length; k < length3; k++){
    var chapter = this.metadata.chapters[k];
    console.log(chapter.id, id, chapter.id === id)
    if (chapter.id === id) {
      //matched id 
      this.metadata.chapters.splice(k, 1);
      flag = 1
      break
    }
  }
  if (flag) {
    cb(null, self.metadata) // saved later
  } else {
    cb('Chapter with id => '+ id + ' not found', self.metadata)
  }
}

noteProto.deleteFile = function (id, cb) {
  gutil.deleteFile(id, cb);
}

noteProto.deleteTopic = function (id, cb) {
  var flag = 0;
  // find chapter
  for(var k = 0, length3 = this.metadata.chapters.length; k < length3; k++){
    var chapter = this.metadata.chapters[k];
    for(var j = 0, length2 = chapter.topics.length; j < length2; j++){
      var topic = chapter.topics[j];
      if (topic.id  === id) {
        chapter.topics.splice(j, 1);
        //matched id 
        console.log(j)
        flag = 1
        break
      }
    }
    if (flag) {break}
  }
  if (flag) {
    this.updateNoteMetadata((res)=>{
      // delete the topic-name.md file
      gutil.deleteFile(id, cb);
    })
  }
}

/**
 * 1. deleteTopicSync
 * 2. updateMetadata
 * 3. deleteFile
 */

noteProto.deleteTopicSync= function (id, cb) {
  var flag = 0;
  // find chapter
  for(var k = 0, length3 = this.metadata.chapters.length; k < length3; k++){
    var chapter = this.metadata.chapters[k];
    for(var j = 0, length2 = chapter.topics.length; j < length2; j++){
      var topic = chapter.topics[j];
      if (topic.id  === id) {
        chapter.topics.splice(j, 1);
        //matched id 
        console.log(j)
        flag = 1
        break
      }
    }
    if (flag) {break}
  }
  if (flag) {
    cb(null, this.metadata) // saved later
    // this.updateNoteMetadata((res)=>{
      // delete the topic-name.md file
      // gutil.deleteFile(id, cb);
    // })
  } else {
    cb('Topic with id:'+ id + ' not found', this.metadata)
  }
}

noteProto.updateNoteMetadata = function (cb, data) {
  gutil.uploadFile(this.noteMetaId, '/', JSON.stringify(this.metadata), cb, data);
}

noteProto.updateNoteMetadataSync = function (data, cb) {
  if (window.env === 'dev') {
    setTimeout(()=>{
      // CNote.saveMetadata(data)
      cb(null, 'dev metadata updated.')
    }, 1000)
    return
  }
  this.metadata = data
  gutil.uploadFile(this.noteMetaId, '/', JSON.stringify(data), cb);
}

export default Note;


