/* eslint-disable no-alert, no-console */
import localStore from 'store'
/* eslint-enable no-alert, no-console */

import addTaskFn from './../addTask/addTask'
import { log, logStore } from './../utils'
import execute from './execute'

window.localStore=localStore

const async = {
  note: {
    // noteId: 1
  }, // Note Object will be assigned hete
  metadata: {},
  tasks: [], // load task from localStorage
  running: false,
  lastUpdateCall: 0,
  addTask: function (task) {
    addTaskFn(this, task) // some new tasks are assigned
  },
  dumpTasks: function () {
    console.log('Dumped tasks => ', [].concat(this.tasks))
    this.tasks = []
    this.running = false
    localStore.remove('tasks')
  },
  loadTasks: function () {
    if(localStore.get('updateRemaining')){
      var tasks = localStore.get('tasks')
      var oldTasks = []
      for (var key in tasks) {
        tasks[key].message = '***OLD*** task loaded from local store'
        oldTasks.push(tasks[key])
      }
      this.tasks =  oldTasks

      // *** assign cleaning task

      log(this.tasks)
      if (this.tasks.length) {
        log('Running loaded', this.tasks.length , 'tasks')
        this.run() 
      }
    }
  },
  timeStamp: function (){
    this.lastUpdateCall = ((new Date()).getTime())
    localStore.set('lastUpdateCall', this.lastUpdateCall)
    return this.lastUpdateCall
  },
  assertNote: function (task, cb1) {
    if (this.note.noteId === task.payload.noteId) {
      cb1()
    } else {
      // this.tasks.unshift(Object.assign({}, task))
      this.tasks.unshift({
        type: '@CREATE_NOTE',
        payload: {noteId: task.payload.noteId}
      })
      this.run()
    }
  },
  next: function (skip) {
    var task = this.tasks.shift()
    localStore.set('tasks', Object.assign({}, this.tasks)) // update localstorage to update later
    // console.log(localStore.get('tasks'))
    // console.log(this.note.metadata.updatedAt)
    if (skip) {
      log('SKIPPING => ',task.type)
    }
    logStore.flushInLine()
    this.run()
  },
  run: function () {
    if (this.tasks.length===0) { 
      log('All tasks finished...waiting...')
      this.running = false
      localStore.set('updateRemaining', false)
      localStore.set('tasks', [])
      return
    }
    this.running = true
    this.execute()
  },
  execute: function () {
    execute(this)
  }
}

export default async;


