import React, { Component } from 'react';
import { Route, HashRouter as Router, Switch, Link } from 'react-router-dom';
import './index.scss';

class SignInUp extends Component {
  render() {
    return (
      <div className="nav-links">
        <p><Link to={`/login`}>Sign in</Link> <span className="or-spacer">or</span> <Link to={`/`}>Sign up</Link></p>
      </div>
    );
  }
}

class SignedIn extends Component {
  render() {
    return (
        <div className="nav-links">
          {
            this.props.navlinks ? 
              (<span>
                <div className="k-sm-btn">Gdrive In</div>
                <div className="k-sm-btn">New Note</div>
              </span>) : ''
          }
          <div className="hw-40 c-e3 nav-avatar">12</div>
        </div>
    );
  }
}
function SignedInNoteBook () {
  return <SignedIn navlinks={true}/>
}

function SignedInNoteRead () {
  return <SignedIn navlinks={false}/>
}

function SignedInNoteEdit () {
  return <SignedIn navlinks={false}/>
}


class SearchField extends Component {
  constructor(props) {
    super(props)
    this.state = {
      searchFileterText: '',
      placeholder: 'Search noteapp',
      leftPadding: '0px'
    }
    this.style = {}
  }

  evaluateSearchFilter () {
    var matchPath = this.props.match.path
    
    if (matchPath === '/note/:noteId') {
      this.setState({
        searchFileterText: 'Search this note'
      })
    } else if (matchPath === '/notebooks') {
      this.setState({
        searchFileterText: 'Search your notes'
      })
    } else {
      this.setState({
        searchFileterText: 'Search all'
      })
    }
    this.adjustSearchInputLeftPadding()
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevProps.match.path !== this.props.match.path) {
      this.evaluateSearchFilter()
    }

    // calculate the width of the filter text and 
    // update the left-padding of the input search field
    // console.log(this.refs.searchFilter.style)
  }

  adjustSearchInputLeftPadding () {
    var self = this
    setTimeout(()=>{
      // console.log(self.refs.searchFilter.offsetWidth + (125 - 118) + 'px')
      // !!!VERY BAD solution
      // TODO: change the following code
      self.setState({
        leftPadding: self.refs.searchFilter.offsetWidth + (125 - 118) + 'px'
      })
    },100)
  }

  componentDidMount() {
    this.evaluateSearchFilter()
  }

  render () {
    return (
      <div className="nav-search">
        {this.state.searchFileterText !== '' ? <div ref='searchFilter' className="search-filter">{this.state.searchFileterText}</div> : ''}
        <input type="text" className="form-input" placeholder={!this.state.searchFileterText ? this.state.placeholder:''} style={{paddingLeft: this.state.leftPadding}}/>
      </div>
    )
  }
}

class noopEl extends Component {
  render () {
    return (
      <span></span>
    )
  }
}

class Navigation extends Component {
  render () {
    return (
      <header>
        <div className="nav-header">
          <div className="layout layout-main">
            <div className="brand-title">
              <Link to="/">NoteApp</Link>
            </div>
              <Router>              
                <Switch>
                  <Route path="/note/:noteId" component={SearchField}/>
                  <Route path="/notebooks" component={SearchField}/>
                  <Route path="/login" component={noopEl}/>
                  <Route path="/" component={SearchField}/>
                  <Route exact path="/" component={SearchField}/>
                </Switch>
              </Router>
            <Router>
              <Switch>
                <Route path="/notebooks" component={SignedInNoteBook}/>
                <Route path="/note/read/:noteId/:topic" component={SignedInNoteRead}/>
                <Route path="/note/edit/:noteId" component={SignedInNoteEdit}/>
                <Route path="/" component={SignInUp}/>
              </Switch>
            </Router>
          </div>
        </div>
      </header>      
    )
  }
}

export default Navigation;



