import { consistentgIndex, findDownPosition, addNoteClass, findParent } from './util';

const onMouseMove = (oldState, el, event) => {
  var state = oldState
  var notes = state.notes,
      dropIndex = -1,
      moveEl = '',
      elChildren = [],
      parentIndex = -1
  if (state.mousedown) {
    if (state.elId !== '' && state.elId === el.id) {
      moveEl = el
    }
    // if (this.target.className.split(' ').indexOf('up-2') === -1) {
    //   this.target.className = this.target.className + ' up-2';
    // } else {
    // remove the el

    if (moveEl.id === state.elId) {
      var index = el.gIndex
      if (el.type === 'chapter' && notes.length === 1) {  // only single item exists
        return state
      }
      if (index !== -1) {
        // remove chapter and its topics
        if (notes[index].type === 'topic') {
          parentIndex = findParent(notes, index)
          notes[parentIndex].children--
        }

        notes = notes.slice(0, index).concat(notes.slice(index+1)) // create new notes object

        // SAVE topics
        if (el.type === 'chapter' && el.children !== 0) {
          // ss rotates at same position
          for (var ss = index;;) {
            if (notes[ss] && notes[ss].type === 'topic') {
              elChildren.push(Object.assign({}, notes[ss]))
              notes.splice(ss, 1)
              if (!notes[ss] || notes[ss].type === 'chapter') {
                break;
              }
            } else {
              break
            }
          }
        }
        
        // handle last element dragging
        if (el.gIndex === notes.length) {
          dropIndex = el.gIndex
          notes = addNoteClass(notes, dropIndex-1, 'bottom-4')
        }

        notes.forEach((el, index) => {el.gIndex = index}); 
      }
      return Object.assign({}, state, { 
        notes,
        elChildren, 
        dropIndex, 
        dropEl: notes[dropIndex-1], 
        el: moveEl 
      })
    }

    if(el.id !== state.el.id) {
      var rectObject = event.target.getBoundingClientRect();
      var onBottomHalf = false
      if (event.clientY > (rectObject.top + (rectObject.bottom-rectObject.top)/2)) {
        onBottomHalf = true
      }
      state.dropEl = el
      consistentgIndex(notes, (_el, _index)=>{
        if (_el.id === el.id) { index = _index }
      })

      if (state.el.type === 'chapter') {
        if (onBottomHalf) {
          if (el.type === 'chapter') {
            if (notes[index+1] && notes[index+1].type === 'chapter') {
              notes = addNoteClass(notes, index, 'bottom-4')
              dropIndex = index+1
            } else {
              // go to end
              dropIndex = findDownPosition(notes, index)
              notes = addNoteClass(notes, dropIndex-1, 'bottom-4')
            }
          }
          if (el.type === 'topic') {
            // go to end
            dropIndex = findDownPosition(notes, index)
            notes = addNoteClass(notes, dropIndex-1, 'bottom-4')
          }
        } else {
          // onTopHalf
          if (el.type === 'chapter') {
            if (index === 0) {
              notes = addNoteClass(notes, index, 'top-4')
            } else {
              notes = addNoteClass(notes, index-1, 'bottom-4')
            }
            dropIndex = index
          } else {
            // go to end
            dropIndex = findDownPosition(notes, index)
            notes = addNoteClass(notes, dropIndex-1, 'bottom-4')
          }
        }
      } else {
        // this.el.type === 'topic'
        if (onBottomHalf) {
            notes = addNoteClass(notes, index, 'bottom-4')
            dropIndex = index+1
          } else {
            // if el is last element
            if (index === 0) {
              notes = addNoteClass(notes, index, 'bottom-4')
              dropIndex = 1
            } else {
              notes = addNoteClass(notes, index-1, 'bottom-4')
              dropIndex = index
            }
        }
      }
    }
    return Object.assign({}, state, { notes, dropIndex })
  }
  return state
}

const onMouseUp = (state) => {
  var notes = state.notes,
      parentIndex = -1
  // Used for cloud sync
  // var elPrev = Object.assign({}, this.el)
  // var elIndex = 0;

  // drop the el at dropIndex
  if (state.dropEl && state.el !== '' && state.dropEl.id !== state.el.id) {
    // drop the item
    notes = notes.slice(0, state.dropIndex)
              .concat([state.el])
              .concat(notes.slice(state.dropIndex));
    if (state.el.type === 'topic') {
      parentIndex = findParent(notes, state.dropIndex)
      if (parentIndex === -1) { console.error('Error: Found negative array element index.') }
      notes[parentIndex].children++
      notes[state.dropIndex].parentId = notes[parentIndex].id
    }

    // when elChildren is empty this code does not run
    if (state.elChildren.length) {
      notes = notes.slice(0, state.dropIndex+1)
                .concat(state.elChildren)
                .concat(notes.slice(state.dropIndex+1));
    }
    // process the chapters list
    // sanitize and save
    // processContetnOrderChange(chapters[elIndex], elPrev, chapters);
  }

  consistentgIndex(notes)
  return Object.assign({}, state, {
    notes,
    el: '',
    elId: '',
    mousedown: false,
    dropEl: '',
    elChildren: []
  })
}

export {
  onMouseMove,
  onMouseUp
}


