import React, { Component } from 'react';

 class ContentList extends Component {
  constructor (props) {
    super(props)
    var self = this

    // local state handler
    this.editOldName = ''
    this.creatingTopic = false

    // async.createMetadata(this.props.parent.state.chapters)
    // create the Note object here pass inside async
    window.onmouseup = function (e) {
      self._onMouseUp(e)
    }
    window.onmousedown = function (e) {
      if (self.props.isContextMenu) {
        self.props.hideContextMenu()
      }
    }
  }

  _contextMenu (e, el) {
    e.preventDefault();
    // console.log(e)
    this.props.showContextMenu(e, el)
    // addMenu.popup(e.clientX, e.clientY);
  }

  _onMouseMoveInput (e) {
    e.stopPropagation()
  }

  _onMouseDown (el, event) {
    if (!this.props.editing) {
      event.preventDefault()
    }
    this.props.onMouseDown(el) 
    return    
  }

  _onMouseUp (event) {
    this.props.onMouseUp()
    return
  }

  // _onMouseMove (el, event) {}
  _onMouseMove (el, event) {
    if (!this.props.editing) {
      event.preventDefault()
    } else {
      return
    }
    if (this.props.mousedown) {
      this.props.onMouseMove(el, event)
    }
    return
  }

  _onLinkClick (e, el) {
    if (this.props.noteId) {
      var path = `/note/edit/${this.props.noteId}/${el.gIndex}`
      this.props.history.push(path)
    }
  }

  onChange (e, el) {
    this.props.onChange(el, e.target.value)
  }


  _commmitLinkEdit () {
    // this.props.setEditing(false)
    this.props.commitElName(this.props.noteItems[this.props.editElIndex], this.props)
    // this.props.setEditEl(-1)
  }
  
  onKeyPress (e, el) {
    if (e.key === 'Enter') {
      this._commmitLinkEdit()
      // this.registerRenameAsyncTask(el)
    }
  }

  _onBlur () {
    if (!this.skipBlur) { // ad-hok blur skipping as already called _onBlur at _onLinkClick()      
      this._commmitLinkEdit()
    }
    // sync columnWidth = 'inherit'
    // this.registerRenameAsyncTask(el)
  }

  _editLink (e, el) {
    e.stopPropagation() 
    this.props.setEditing(el.gIndex, true)
    this.props.setEditEl(this.props.noteId, el.gIndex)
    // sync cloud
  }

  _addTopic (e, el) {
    e.stopPropagation() 
    this.props.addTopic(el, this.props.noteId, this.props.history)
  }

  componentDidUpdate(prevProps, prevState) {
    var params = this.props.match.params
    var prevParams = prevProps.match.params
    var props = this.props

    if (props.to !== prevProps.to) {
      props.history.push(props.to)
      // this.activateNoteItemFromIndex(params.index)
    } else {
      if (props.noteItems && (params.index < props.noteItems.length)) {
        if (params.index !== prevParams.index) {
          this.activateNoteItemFromIndex(params.index)
        }
      }
    }
    
    if (props.editing && this.refs.editInput) {
      this.refs.editInput.focus()
      this.editOldName = this.refs.editInput.value
    }
  }

  activateNoteItemFromIndex (index) {
    this.props.setActiveLink(index)
    this.props.setActiveEditor(index, this.props.noteItems)
  }

  componentDidMount() {
    // var props = this.props
    // var params = this.props.match.params
    // if (props.noteItems && (params.index < props.noteItems.length)) {
    //   this.activateNoteItemFromIndex(params.index)
    // } 
  }

  /**
   * acivate link on select
   * creates the content list sidebar content
   */
  render () {
    var className = ''
    // var addEl = (this.props.chapters) ? <span className="c-ctrl add-topic">+</span> : ''
    var listEl = this.props.noteItems.map((el, index)=>{
      var addEl = ''
      if (el.type === 'chapter') {
        addEl = <span className="c-ctrl add-topic" onMouseUp={(e)=>{this._addTopic(e, el)}}>
                  <span className="glyphicon glyphicon-plus form-control-feedback" aria-hidden="true"></span>
                </span>
      }
      var showPencil = this.props.activeLinkId === el.localId
      var showEditIcon = (this.props.editors && this.props.editors[el.localId] && this.props.editors[el.localId].edit) || false
      showEditIcon = showEditIcon && !showPencil      
      
      // RENDER chapters and topics
      if ((el.type === 'chapter' && el.children === 0) || el.type === 'topic') {
        var liTopic = ''
        if (el.type === 'topic') {
          liTopic = 'li-topic'
        }

        className = 'note-link-topic '
        className += el.disabled ? 'disabled ':''
        className += el.selected ? 'active':''

        // console.log(showEditIcon, this.props.editors[el.localId])
        return <li className={"simple-li " + liTopic + " el-" + el.localId} key={index}
          onContextMenu={(e)=>{this._contextMenu(e, el)}}

          onMouseDown={(e)=>{
            this._onMouseDown(el, e)
          }}
          onMouseUp={(e)=>{
            this._onLinkClick(e, el)
          }}
        
          onMouseMove={(e)=>{this._onMouseMove(el, e)}}>
          
          {(el.edit)?<input ref="editInput" type="text" value={el.name} 
            onKeyPress={(e)=>{this.onKeyPress(e, el)}} 
            onChange={(e)=>{this.onChange(e, el)}}
            onMouseMove={this._onMouseMoveInput.bind(this)}
            onBlur={this._onBlur.bind(this)}
          />:''}

          {(!el.edit)?<span className={className} 
            >
            {showEditIcon ? <i className="note-sidebar-icon note-editing mdi mdi-pencil"></i>:''}
            {showPencil ? <i className="note-sidebar-icon note-editing mdi mdi-chevron-right"></i>:''}
            {el.name}
            <span className="c-ctrl edit-name" onMouseUp={(e)=>{this._editLink(e, el)}}>
              <span className="glyphicon glyphicon-pencil form-control-feedback" aria-hidden="true"></span>
            </span>
            {addEl}
            </span>:''}

          {el.gIndex === 0 && el.class === 'top-4' ? <div className="top-4"></div>:''}
          {el.class === 'bottom-4' ? <div className="bottom-4"></div>:''}
        </li>
      } else {
        className = 'note-link-chapter bold '
        if(el.disabled){
          className += 'disabled '
        }
        if(el.selected){
          className += 'active'
        }

        return <li className={"simple-li el-" + el.localId} key={index}
          onContextMenu={(e)=>{this._contextMenu(e, el)}}

          onMouseDown={(e)=>{
            this._onMouseDown(el, e)
          }}
          onMouseUp={(e)=>{
            this._onLinkClick(e, el)
          }}

          onMouseMove={(e)=>{this._onMouseMove(el, e)}}>

          {(el.edit)?<input ref="editInput" type="text" value={el.name} 
            onKeyPress={(e)=>{this.onKeyPress(e, el)}} 
            onChange={(e)=>{this.onChange(e, el)}}
            onMouseMove={this._onMouseMoveInput.bind(this)}
            onBlur={this._onBlur.bind(this)}
            />:''}

          {(!el.edit) ? <span className={className}
            >
            {showEditIcon ? <i className="note-sidebar-icon note-editing mdi mdi-pencil"></i>:''}
            {showPencil ? <i className="note-sidebar-icon note-editing mdi mdi-chevron-right"></i>:''}
            {el.name}
            {addEl}
            <span className="c-ctrl edit-name" onMouseUp={(e)=>{this._editLink(e, el)}}>
              <span className="glyphicon glyphicon-pencil form-control-feedback" aria-hidden="true"></span>
            </span>
          </span>:''}

          {el.gIndex === 0 && el.class === 'top-4' ? <div className="top-4"></div>:''}
          {el.class === 'bottom-4' ? <div className="bottom-4"></div>:''}
        </li>
      }
    })

    return (
      <ul className="simple-ul no-bold">
        {listEl}
      </ul>
    )
  }
}

export default ContentList;


