import React, { Component } from 'react';
import './context-menu.scss';

class ContextMenu extends Component {
  onMouseDown (e) {
    e.preventDefault()
    e.stopPropagation()
  }

  render() {
    var ret = ''
    var self = this
    var ctxItems = this.props.items.map((item, index)=>{
      if (item.type === 'ctx-handle') {
        ret = <li className="li-ctx-menu" key={index} onMouseDown={self.onMouseDown} onMouseUp={()=>{item.onClick(this.props.history)}}>{item.nameIndex !==undefined ? item.name[item.nameIndex]: item.name}</li>
      } else if (item.type === 'separator') {
        ret = <li className="ctx-separator" key={index} onMouseDown={self.onMouseDown}></li>
      }
      return ret
    })
    return (
      <div className="context-menu" style={this.props.style}>
        <ul className="ul-ctx-menu">
          {ctxItems}
        </ul>
      </div>
    );
  }
}

export default ContextMenu


