import Note from './ContextMenu'
import { connect } from 'react-redux'

const mapStateToProps = (state) => {
  return {
    contextMenuStyle: state.note.contextMenuStyle
  }
}

const mapDispatchToProps = (dispatch) => {
  return {}
}

const ConnectedContextMenu = connect(
  mapStateToProps,
  mapDispatchToProps
)(Note)

export default ConnectedContextMenu


