const consistentgIndex = (notes, cb) => {
  notes.forEach((el, index) => {
    el.gIndex = index; 
    el.class = '';
    if (typeof(cb) === 'function') {
      cb(el, index)
    }
  });
}

const findDownPosition = (_notes, _index) => {
  for (var start = _index, end = _notes.length - 1; start <= end; start++) {
    if (!_notes[start+1] || !_notes[start+1].parentId) {

      // Use it after getting the index
      // _notes[start].class = 'bottom-4'
      // this.dropIndex = start+1
      return start+1
    }
  }
}

const findParent = (_notes, _index) => {
  for (var index = _index; index > -1; index--) {
    if (_notes[index].type === 'chapter') {
      return index
    }
  }
  return -1
}

const addNoteClass = (notes, index, className) => {
  notes[index].class = className
  return notes.slice(0, index)
          .concat([notes[index]])
          .concat(notes.slice(index+1))
}

export {
  consistentgIndex,
  findDownPosition,
  addNoteClass,
  findParent
}


